@import url(https://fonts.googleapis.com/css?family=Seaweed+Script&display=swap);
@import url(https://fonts.googleapis.com/css?family=Prompt:400,400i,700&display=swap);
.header{z-index:1;font-family:'Seaweed Script', cursive;display:flex;justify-content:center;height:70px;background:#FFFFFF;box-shadow:0px 4px 4px rgba(0,0,0,0.25);position:fixed;width:100%}.header__h1{font-style:normal;font-weight:normal;font-size:30px;line-height:41px;letter-spacing:0.51em;color:#FF8A00;margin-top:13px 0}.header__logo{height:50px;width:50px;margin-top:8px}

.flex-wrap{background:url(/cv/static/media/fon.087d7df6.jpg);min-height:100vh;display:flex;padding:60px 0 0 230px;position:relative}.pen{background:url(/cv/static/media/pen.486cc026.png);width:66px;height:512px;background-size:66%;background-repeat:no-repeat;position:absolute;left:129px;top:110px}.cup{background:url(/cv/static/media/cup.99b7a9a8.png);width:179px;height:211px;position:absolute;left:946px;top:64px}.flower{background:url(/cv/static/media/flower.d15bada6.png);width:411px;height:489px;position:absolute;left:747px;top:510px}.cv{position:relative;width:690px;height:812px;background-color:white;box-shadow:36px 36px 82px 1px rgba(0,0,0,0.49);border-radius:1px}.cv__grid-wrapper{display:grid;grid-template-columns:200px 6fr}.cv__main-photo{background:url(/cv/static/media/photo1.3dc752d2.jpg);background-size:153%;background-position:-58px -20px;height:292px}.cv__contactme{position:relative;margin-top:4px;list-style-type:none;text-align:right}.cv__contactme img{height:15px;position:relative;left:-3px;top:3.4px}.cv__text-contact{position:absolute;top:12px;right:42px;font-size:9px;letter-spacing:0.7px}.cv__contact-styles{margin-bottom:-8px;display:block;font-size:11px;text-transform:uppercase}.cv__contacts{padding-left:16px}.cv__education{margin-top:-249px;background:#6a6a6b;color:white;height:260px}.cv__education-title{font-size:12px;letter-spacing:1.4px;background:#d8ba43;color:#3d4047;font-weight:bold;margin:0;padding:11px 22px;text-transform:uppercase;text-align:right}.cv__icon-border{border:1px solid;border-radius:50px;width:24px;height:24px;margin-top:13px;display:inline-block;cursor:pointer}.cv__home-link{color:white}.cv__icon-border1 img{position:relative;left:-5px;top:4px}.cv__icon-border2 img{position:relative;left:-0.6px;top:2px;width:22px;height:21px}.cv__icon-border3 img{position:relative;left:-5px;top:4px}.cv__icon-border4 img{position:relative;left:-5px;top:4px;height:14px}.cv__telegram-link{text-decoration:none;color:white}.cv__status{background:#40413c;text-align:left;height:17px;padding:7px;font-size:14px;color:#d2c166;background-position:-36px -88px;background-size:1118px;background-repeat:no-repeat;text-transform:uppercase}.cv__name{text-align:left;font-family:'Russell';letter-spacing:1px;margin:44px 0 5px;text-transform:uppercase}.cv__address{font-size:10px;text-align:left;color:#383838;padding:10px 0 0 2px;text-align:left;font-weight:bold;font-style:italic}.cv__square{width:47px;height:10px}.cv__main-info{padding-left:50px}.cv__contacts-wrap{height:252px;background:#41414a;color:white;padding:9px 21px 0 0;position:relative}.cv__contacts-wrap h3{text-transform:uppercase;font-size:15px;color:#cab200;border-bottom:2px solid #cab200;padding-bottom:4px;font-family:"roboto";text-align:right}.cv__accent-yellow{width:27px;height:4px;background-color:#cab200;position:absolute;right:21px;top:28px}.cv__accent-gray{width:122px;height:4px;background-color:#828282;position:absolute;left:52px;top:29px}.cv__accent-gray2{top:111px}.cv__main-row{display:grid;grid-template-columns:200px 6fr;position:absolute;top:184px;left:auto;right:0;height:88px;width:100%;padding:10px 0 0}.cv__profile-title{padding-top:4px;padding-right:20px;font-size:14px;letter-spacing:1.9px;line-height:22px;color:white;text-align:right;text-transform:uppercase;letter-spacing:1.5px;text-transform:uppercase;background-color:#a98500b8}.cv__profile-description{text-align:left;color:white;font-size:10px;line-height:18px;letter-spacing:1.3px;padding:4px 6px 4px 50px;background:#c5ad4d}.cv__exp{position:relative}.cv__exp-pe-description{font-size:11px;height:17px;margin:0 0 0 77px;padding-bottom:6px;text-align:left}.cv__exp-pe-title{margin:24px 0 15px 52px;font-size:12px;font-weight:bold;text-align:left;padding:4px 0 5px;color:#41414a;letter-spacing:0.8px;text-transform:uppercase;border-bottom:2px solid #80808091}.cv__exp-pe-title-1{padding-bottom:10px;margin-top:15px}.cv__skillset{font-size:12px;text-align:left;margin-left:174px;border-left:2px solid #cecece}.cv__item-wrap{line-height:23px;position:relative;left:-98px}.cv__skill{display:flex;text-align:left;padding:0 20px 0 100px;font-size:11px;align-items:baseline}.cv__item{font-weight:bold;font-size:12px;position:absolute;width:98px;border-bottom:2px solid #cecece}.cv__hr{width:213px;background-color:#cecece;border:none;color:#cecece;background-color:#cecece;height:2px;position:absolute;left:97px;bottom:-8px}.cv__vuz{display:block;padding:12px 20px 0;font-size:9px;text-align:right;letter-spacing:0.67px}.cv__vuz span{margin-bottom:-4px;letter-spacing:1px;display:block;font-size:11px;text-transform:uppercase}

.tooltip{position:relative;display:inline-block}.tooltip .tooltiptext{visibility:hidden;width:140px;background-color:#555;color:#fff;text-align:center;border-radius:6px;padding:5px;position:absolute;z-index:1;bottom:150%;left:50%;margin-left:-75px;opacity:0;transition:opacity 0.3s}.tooltip .tooltiptext::after{content:"";position:absolute;top:100%;left:50%;margin-left:-5px;border-width:5px;border-style:solid;border-color:#555 transparent transparent transparent}.tooltip:hover .tooltiptext{visibility:visible;opacity:1}

.App{text-align:center;font-family:'Prompt', sans-serif}.header__img{width:100%}@font-face{font-family:'Neverwinter';src:url(/cv/static/media/Neverwinter.409059b6.ttf) format("truetype");font-weight:normal;font-style:normal}@font-face{font-family:'Russell';src:url(/cv/static/media/Russell.05788439.otf) format("truetype");font-weight:normal;font-style:normal}

